.MyMessage {
    display: flex;
    justify-content: flex-end;
    
}

.MyMessage p {
    background-color: rgb(24, 126, 243);
    padding: 1%;
    border-radius: 100px 100px 0 100px;
}

.AlienMessage p {
    background-color: rgb(4, 207, 92);
    padding: 1%;
    border-radius: 100px 100px 100px 0;
}

.AlienMessage {
    display: flex;
    justify-content: flex-start;
}