.list {
    display: flex;
    flex-direction: column;
    grid-row: 2 span;
}

.Dialogs {
    display: grid;
    grid-template-columns: 2fr 3fr;
    color: white;
    margin-top: 5%;
}

.name {
    color: white;
    text-decoration: none;
}

.Dimg {
    border-radius: 1000px;
    width: 100%;
}

.oneDialog p {
    height: 30px;
}

.message {
    background: rgb(116, 116, 116);
}

.oneDialog {
    display: grid;
    grid-template-columns: 1fr 5fr;
}

.NewMessage {
    display: flex;
}

.NewMessage textarea {
    width: 100%;
}




